import * as React from "react";
import Checkbox from "@mui/material/Checkbox";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormLabel from "@mui/material/FormLabel";
import Grid from "@mui/material/Grid2";
import OutlinedInput from "@mui/material/OutlinedInput";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import { styled } from "@mui/system";

const FormGrid = styled(Grid)(() => ({
  display: "flex",
  flexDirection: "column",
  "& .MuiOutlinedInput-root": {
    borderRadius: "8px",
  },
}));

export default function RegistrationForm() {
  const [useType, setUseType] = React.useState("personal");

  return (
    <Grid container spacing={3} sx={{ direction: "ltr" }}>
      <FormGrid size={{ xs: 12 }}>
        <FormLabel htmlFor="use-type" required>
           
        </FormLabel>
        <Select
          id="use-type"
          value={useType}
          onChange={(e) => setUseType(e.target.value)}
          size="small"
        >
          <MenuItem value="personal"> </MenuItem>
          <MenuItem value="business"> </MenuItem>
          <MenuItem value="academic"> </MenuItem>
        </Select>
      </FormGrid>

      <FormGrid size={{ xs: 12, md: 6 }}>
        <FormLabel htmlFor="first-name" required>
           
        </FormLabel>
        <OutlinedInput
          id="first-name"
          name="first-name"
          type="text"
          placeholder=""
          autoComplete="given-name"
          required
          size="small"
        />
      </FormGrid>

      <FormGrid size={{ xs: 12, md: 6 }}>
        <FormLabel htmlFor="last-name" required>
           
        </FormLabel>
        <OutlinedInput
          id="last-name"
          name="last-name"
          type="text"
          placeholder=""
          autoComplete="family-name"
          required
          size="small"
        />
      </FormGrid>

      <FormGrid size={{ xs: 12 }}>
        <FormLabel htmlFor="email" required>
           
        </FormLabel>
        <OutlinedInput
          id="email"
          name="email"
          type="email"
          placeholder="your@email.com"
          autoComplete="email"
          required
          size="small"
        />
      </FormGrid>

      <FormGrid size={{ xs: 12 }}>
        <FormLabel htmlFor="organization">
           {useType === "personal" ? "()" : ""}
        </FormLabel>
        <OutlinedInput
          id="organization"
          name="organization"
          type="text"
          placeholder="   "
          required={useType !== "personal"}
          size="small"
        />
      </FormGrid>

      <FormGrid size={{ xs: 12, md: 6 }}>
        <FormLabel htmlFor="phone" required>
          
        </FormLabel>
        <OutlinedInput
          id="phone"
          name="phone"
          type="tel"
          placeholder="050-0000000"
          autoComplete="tel"
          required
          size="small"
        />
      </FormGrid>

      {useType === "business" && (
        <FormGrid size={{ xs: 12, md: 6 }}>
          <FormLabel htmlFor="company-id" required>
            . /  
          </FormLabel>
          <OutlinedInput
            id="company-id"
            name="company-id"
            type="text"
            placeholder=" .   "
            required
            size="small"
          />
        </FormGrid>
      )}

      {useType === "academic" && (
        <FormGrid size={{ xs: 12, md: 6 }}>
          <FormLabel htmlFor="institution" required>
             
          </FormLabel>
          <Select id="institution" size="small" defaultValue="">
            <MenuItem value=""> </MenuItem>
            <MenuItem value="tau">  </MenuItem>
            <MenuItem value="huji"> </MenuItem>
            <MenuItem value="technion"></MenuItem>
            <MenuItem value="bgu">  </MenuItem>
            <MenuItem value="haifa"> </MenuItem>
            <MenuItem value="other"></MenuItem>
          </Select>
        </FormGrid>
      )}

      <FormGrid size={{ xs: 12 }}>
        <FormControlLabel
          control={<Checkbox name="newsletter" value="yes" defaultChecked />}
          label="       "
        />
      </FormGrid>

      <FormGrid size={{ xs: 12 }}>
        <FormControlLabel
          control={<Checkbox name="terms" value="yes" required />}
          label={
            <span>
                 
              <a
                href="/terms"
                style={{
                  color: "primary",
                  textDecoration: "underline",
                  margin: "0 4px",
                }}
              >
                 
              </a>
              
              <a
                href="/privacy"
                style={{
                  color: "primary",
                  textDecoration: "underline",
                  margin: "0 4px",
                }}
              >
                 
              </a>
            </span>
          }
        />
      </FormGrid>
    </Grid>
  );
}
